.draftrounditem {
    display: flex;
    justify-content: flex-end;
    width:22%;
    height: 60px;
    align-items: center;
    padding:10px;
    border-radius: 7px;

    background:#f6f7f8;
}
.draftrounditem label{
    width: 140px;
}

.draftrounditem input {
    font-size: 14px;
    height:24px;
    max-width: 10vw;
}

.draftrounditem img {
    padding: 0px;
    padding-left:5px;
    margin: 10px;
}