.appcontainer {
	display: flex;
	flex-flow: column wrap;
	justify-content: center;
	align-items: center;
	font-family: 'RobotoDraft', 'Roboto', sans-serif;
	background-color: #DAE0E6;
	width: 1500px;
	padding: 30px 20px;
	border-radius: 7px;
}

.elementcontainer {
	background-color: white;
	padding: 30px 50px;
	border-radius: 7px;
	margin: 20px;
}

.appcontainer .elementcontainer h1 {
	margin: 0 0 20px;
    font-size: 72px;
    font-weight: 300;
}

.appcontainer .elementcontainer h2 {
	margin: 0 0 20px;
    font-size: 36px;
    font-weight: 300;
}

.elementcontainer img {
	padding: 30px;
}