.draftreview {
    * {
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        font-family: arial;
      }
      
      body {
        background-color: #616161;
        font-family: arial;
        padding: 15px;
      }
      
      /*======================*/
      .radio-box {
        position: relative;
        width: 300px;
        margin: 25px auto;
        -moz-border-radius: 4px;
        -webkit-border-radius: 4px;
        border-radius: 4px;
        border: 1px solid #ccc;
        -moz-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
        -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
        background-color: #DFDFD1;
      }
      
      .radio-label {
        display: block;
        color: #555;
        font-size: 24px;
        line-height: 1;
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.7);
        -moz-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
        -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
        box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
        cursor: pointer;
        padding: 15px 10px 15px 60px;
        height: 50px;
      }
      .radio-label:last-of-type {
        -moz-box-shadow: 0 0 0;
        -webkit-box-shadow: 0 0 0;
        box-shadow: 0 0 0;
        border: none;
      }
      
      .radio-btn {
        width: 100%;
        position: relative;
        height: 50px;
        position: absolute;
        top: 50px;
        opacity: 0;
        z-index: 2;
        cursor: pointer;
        bottom: 0;
        right: 0;
      }
      
      .marker {
        position: absolute;
        width: 30px;
        height: 30px;
        z-index: 2;
        display: inline-block;
        background-color: rgb(179, 41, 53);
        top: 50px;
        left: -1px;
        background-color: rgb(179, 41, 53);
        background-image: url('data:image/svg+xml;base64,');
        background-size: 100%;
        background-image: -webkit-gradient(linear, 0% 50%, 100% 50%, color-stop(0%, #d6b810), color-stop(100%, rgb(179, 41, 53)));
        background-image: -moz-linear-gradient(left, #d6b810 0%, rgb(179, 41, 53) 100%);
        background-image: -webkit-linear-gradient(left, #d6b810 0%, rgb(179, 41, 53) 100%);
        background-image: linear-gradient(to right, #d6b810 0%, rgb(179, 41, 53) 100%);
        -moz-border-radius: 4px 0 0 4px;
        -webkit-border-radius: 4px;
        border-radius: 4px 0 0 4px;
        -moz-transition: all 0.4s;
        -o-transition: all 0.4s;
        -webkit-transition: all 0.4s;
        transition: all 0.4s;
        -moz-animation: markmove 2s;
        -webkit-animation: markmove 2s;
        animation: markmove 2s;
      }
      .marker:after {
        content: '';
        position: absolute;
        border: 16px solid transparent;
        border-left: 15px solid rgb(179, 41, 53);
        left: 100%;
        top: 0;
      }
      
      .radio-box .radio-btn:nth-of-type(1) {
        top: 0px;
      }
      .radio-box .radio-btn:nth-of-type(1):checked ~ .marker {
        top: 10px;
      }
      .radio-box .radio-btn:nth-of-type(1):checked + .radio-label {
        color: rgb(179, 41, 53);
        background-color: #fafaf0;
      }
      .radio-box .radio-btn:nth-of-type(1):hover ~ .marker {
        top: 10px !important;
      }
      
      .radio-box .radio-btn:nth-of-type(2) {
        top: 50px;
      }
      .radio-box .radio-btn:nth-of-type(2):checked ~ .marker {
        top: 60px;
      }
      .radio-box .radio-btn:nth-of-type(2):checked + .radio-label {
        color: rgb(179, 41, 53);
        background-color: #fafaf0;
      }
      .radio-box .radio-btn:nth-of-type(2):hover ~ .marker {
        top: 60px !important;
      }
      
      .radio-box .radio-btn:nth-of-type(3) {
        top: 100px;
      }
      .radio-box .radio-btn:nth-of-type(3):checked ~ .marker {
        top: 110px;
      }
      .radio-box .radio-btn:nth-of-type(3):checked + .radio-label {
        color: rgb(179, 41, 53);
        background-color: #fafaf0;
      }
      .radio-box .radio-btn:nth-of-type(3):hover ~ .marker {
        top: 110px !important;
      }
      
      .radio-box .radio-btn:nth-of-type(4) {
        top: 150px;
      }
      .radio-box .radio-btn:nth-of-type(4):checked ~ .marker {
        top: 160px;
      }
      .radio-box .radio-btn:nth-of-type(4):checked + .radio-label {
        color: rgb(179, 41, 53);
        background-color: #fafaf0;
      }
      .radio-box .radio-btn:nth-of-type(4):hover ~ .marker {
        top: 160px !important;
      }
      
      .radio-box .radio-btn:nth-of-type(5) {
        top: 200px;
      }
      .radio-box .radio-btn:nth-of-type(5):checked ~ .marker {
        top: 210px;
      }
      .radio-box .radio-btn:nth-of-type(5):checked + .radio-label {
        color: rgb(179, 41, 53);
        background-color: #fafaf0;
      }
      .radio-box .radio-btn:nth-of-type(5):hover ~ .marker {
        top: 210px !important;
      }
      
      @-moz-keyframes markmove {
        0% {
          -moz-transform: translateY(-60px);
          transform: translateY(-60px);
        }
        50% {
          -moz-transform: translateY(150px);
          transform: translateY(150px);
        }
        100% {
          -moz-transform: translateY(0);
          transform: translateY(0);
        }
      }
      @-webkit-keyframes markmove {
        0% {
          -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
        }
        50% {
          -webkit-transform: translateY(150px);
          transform: translateY(150px);
        }
        100% {
          -webkit-transform: translateY(0);
          transform: translateY(0);
        }
      }
      @keyframes markmove {
        0% {
          -moz-transform: translateY(-60px);
          -ms-transform: translateY(-60px);
          -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
        }
        50% {
          -moz-transform: translateY(150px);
          -ms-transform: translateY(150px);
          -webkit-transform: translateY(150px);
          transform: translateY(150px);
        }
        100% {
          -moz-transform: translateY(0);
          -ms-transform: translateY(0);
          -webkit-transform: translateY(0);
          transform: translateY(0);
        }
      }
      }